import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import { Layout, Menu, Icon} from 'antd';

const { Header } = Layout;

export default class HeaderPart extends Component{
    render(){
        return (
            <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
                <div className="logo">Snappy</div>
                <Menu
                    theme="dark"
                    mode="horizontal"
                    defaultSelectedKeys={['1']}
                    style={{ lineHeight: '64px', float: "right",marginRight: "10%" }}
                >
                    <Menu.Item key="1"><NavLink to="/"><Icon type="home" /></NavLink></Menu.Item>
                    <Menu.Item key="2"><NavLink to="/about"><Icon type="user"/></NavLink></Menu.Item>
                    <Menu.Item key="3"><NavLink to="/gallery"><Icon type="camera" /></NavLink></Menu.Item>
                    <Menu.Item key="4"><NavLink to="/contact"><Icon type="bulb" /></NavLink></Menu.Item>
                </Menu>
            </Header>
        )
    }
}