import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import { Layout, Button, Affix} from 'antd';
import About from '../about';
import Gallery from '../gallery';
import Contact from '../contact';
const { Content} = Layout;

export default class ContentPart extends Component{
    render(){
        return (
            <Content style={{marginTop: 64, minHeight:"660px"}}>
                <div className="banner">
                    <div className="banner-title">
                        <h1 className="banner-title-h1">perfect photography</h1>
                        <Button className="banner-title-button" type="primary">read more</Button>
                    </div>
                </div>
                <Affix offsetTop={100} className="affix">
                    <Button type="primary">Share</Button>
                </Affix>
                <Route exact path="/"></Route>
                <Route path="/about" component={About}/>
                <Route path="/gallery" component={Gallery}/>
                <Route path="/contact" component={Contact}/>
            </Content>
        )
    }
}